import { FC } from 'react'
import PopupHelp from './PopupHelp.js'
import { v4 } from 'uuid'

/**
 * Simplified LabelInputPairHorizontal component for Ziggy plugins
 */

export interface LabelInputPairHorizontalProps {
  label: string
  className?: string
  inputClassName?: string
  labelClassName?: string
  value: string
  onChange: (value: string) => void
  isNumber?: boolean
  width?: number
  placeholder?: string
  type?: string
  popupHelp?: string
}

const LabelInputPairHorizontal: FC<LabelInputPairHorizontalProps> = ({
  label,
  className = '',
  inputClassName = '',
  labelClassName = '',
  value,
  onChange,
  placeholder = '',
  type = 'text',
  width,
  popupHelp,
}) => {
  const handleChange = (e: React.FormEvent<HTMLInputElement>) => {
    onChange(e.currentTarget.value)
  }

  return (
    <div className={`flex flex-row gap-2 place-items-center ${className}`}>
      <div className="ml-[3px] flex flex-row gap-1 place-items-center">
        <div className={`${labelClassName}`}>{label}</div>
        {popupHelp && <PopupHelp text={popupHelp} />}
      </div>
      <form autoComplete="off">
        <input
          style={{ width: width || '100%' }}
          id={v4()}
          name={v4()}
          className={`border-[1px] border-gray-300 rounded-md p-1 focus:ring-gray-400 ${inputClassName}`}
          placeholder={placeholder}
          autoComplete="off"
          autoCorrect="off"
          autoCapitalize="off"
          spellCheck={false}
          onChange={handleChange}
          onDoubleClick={e => e.stopPropagation()}
          aria-label="input"
          value={value}
          type={type}
        />
      </form>
    </div>
  )
}

export default LabelInputPairHorizontal
